* {
  box-sizing: border-box;
}
.container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: space-between;
  height: 100%;
  width: 412px;
  background-color: #fff;
  border: solid 1px #6a4c93;
}
.NavBar {
  width: 100%;
  height: 8%;
  align-self: flex-start;
  background-color: #448ef8;
}
.Home {
  height: 100%;
}
.Footer {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  background-color: #448ef8;
  flex: 0 1 auto;
}
.name {
  color: #c7c6c6;
  font-family: 'Mallanna', sans-serif;
  font-size: 1.2em;
}
.calldata {
  color: #c7c6c6;
  font-family: 'Mallanna', sans-serif;
  font-size: 1.2em;
}
